*,*::before,*::after{
  box-sizing: border-box;
}

body{
  margin:0;
  padding:0;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container{
  width:100vw;
  height:100vh;
  overflow: hidden;
  position: relative;
  background: rgba(0, 52, 49, 0.7);
  &::after{
    z-index: 0;
    content:'';
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: url('assets/noise.gif');
    opacity: 0.1;
  }
}

.navigation{
  font-family: 'Roboto', sans-serif;
  color:#fff;

  &__link{
    position:absolute;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.22em;
    font-weight: bold;

    &--logo{
      top:12%;
      left:10%;
    }

    &--github{
      bottom:13%;
      left:10%;
    }

    &--cv{
      bottom:13%;
      right:10%;
    }
  }
}

.burger{
  position: absolute;
  top:12%;
  right:10%;

  &__line{
    display: inline-block;
    width:4px;
    height:32px;
    background-color: #fff;
    margin-right:10px;

    &:last-child{
      margin-right: 0;
    }
  }
}

.carousel{
  position: relative;
  z-index: 1;
  top:0;
  left:0;
  width:100vw;
  height:100vh;
  font-family: 'Roboto', sans-serif;

  .queue-slide{
    position:absolute;
    font-weight: 900;
    font-size: 30px;
    line-height: 35px;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
    opacity: 0.3;
    left:13%;

    &--top{
      top:-3%;
    }
    &--bot{
      bottom: -3%;
    }
  }

  .active-slide{
    color:#fff;

    &__text{
      position: absolute;
      font-weight: 900;
      font-size: 36px;
      line-height: 42px;
      -webkit-text-stroke: .5px #fff;
      letter-spacing: 0.065em;
      left:10%;
      top:35%;
      z-index: 1;
      transform: translate(0,0);

      &--outline{
        color:transparent;
        z-index: 3;
      }
    }

    &__image-wrapper{
      z-index: 2;
      position:absolute;
      top:30%;
      left:27%;
      width:100%;

      &::before{
        content:'';
        position: absolute;
        z-index: 2;
        top:0;
        left:0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(233, 138, 21, 0.5) 0%, rgba(0, 52, 49, 0.6) 100%), rgba(21, 225, 152, 0.1);
      }
    }

    &__image{
      width:100%;
      height:auto;
      filter: grayscale(1);
    }
  }
  .slide-count{
    position:absolute;
    bottom:24%;
    right:10%;
    color:#fff;
    font-weight: 700;
    letter-spacing: 0.22em;

    &__current-slide{
      font-size: 36px;
    }

    &__no-slides{
      font-size: 18px;
    }
  }
}